.about {
  height: 100vh;
  background-color: black;
  color: #64ffd3;
}

.about-content {
  margin-left: 10%;
  padding-top: 8%;
  width: 50%;
}

.about-num {
  font-size: 1.5rem;
}

.about-title {
  font-size: 2rem;
  color: aliceblue;
}

.about-text {
  font-size: 1.25rem;
}

.white-text,
.eQ,
.hsbc-text {
