}

ul.skills-list {
  display: grid;
  grid-template-columns: repeat(2, minmax(140px, 200px));
  grid-gap: 0 10px;
  padding: 0;
  margin: 20px 0 0 0;
  overflow: hidden;
  list-style: none;
}

li {
  position: relative;
  margin-bottom: 10px;
  padding-left: 20px;
}

li :before {
  content: "▹";
  position: absolute;
  left: 0;
  line-height: 12px;
  color: aliceblue;
}

@media only screen and (max-width: 736px) {
  .about-content {
    width: 100%;
    margin-left: 2%;
    padding-top: 8%;
  }
}

.exp-page {
  background-color: black;
  height: 100vh;
  color: #64ffd3;
}

.exp-num {
  font-size: 1.5rem;
}

.exp-text {
  font-size: 2rem;
  color: aliceblue;
}

.exp-content {
  margin-left: 10%;
  padding-top: 5%;
}

.persi-exp {
  padding-top: 1%;
}

.persi-logo {
