.exp-companies {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 40px 0;
  max-width: 1200px;
}

.exp-companies .card {
  position: relative;
  box-shadow: inset 5px 5px 5px rgba(140, 207, 238, 0.05);
  border-radius: 15px;
  margin: 30px;
  width: 320px;
  height: 400px;
  background-color: #c4f9ea;
  justify-content: center;
  align-items: center;
}

.exp-companies .card .box {
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  background-color: #203847;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
}

.exp-companies .card .card-content {
  justify-content: center;
  align-items: center;
  text-align: center;
}

.company-title {
  color: aliceblue;
  font-size: 1.3rem;
}

.designation {
  font-size: 1.2rem;
}

.exp-companies .card:hover .box {
  transform: translateY(-50px);
}

