.edu-page {
  height: 120vh;
  background-color: black;
  color: #64ffd3;
}

.edu-num {
  font-size: 1.5rem;
}

.edu-text, .cert-text {
  font-size: 2rem;
  color: aliceblue;
}

.edu-content, .certs {
  margin-left: 10%;
  padding-top: 5%;
}
.ssc, .hsc, .grad{
    background-image: linear-gradient(to bottom right, #64ffd3, #b8fae7);;
    height: 6rem;
    color:black;
    cursor : pointer;
    transition: transform 1s;
    transform-style: preserve-3d;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    filter: blur(0);
  }

.grad{
  height: 8rem;
}

.grad h2{
  padding-top: 2.8rem;
}
.ssc h2, .hsc h2{
  padding-top: 1.3rem;
}

.hsc.back, .ssc.back,.grad.back{
  transform: rotateY(360deg);
}

.hsc.front, .ssc.front,.grad.front{
  text-align: center;
  font-family: cursive;
}

.back-content{
  padding-left: 1rem;
}

.edu-page .row{
  padding-top: 1.5rem;
  overflow-x: auto;
}

.grad-list{
  columns: 2;
  line-height: 1rem;
}

.ssc-list{
  line-height: 1rem;
}

.java-1{
  font-size: 1.5rem;
}
.contact-page {
  height: 110vh;
  background-color: black;
  color: #64ffd3;
}

.con-content {
  margin-left: 10%;
  padding-top: 5%;
}

.contact-num {
  font-size: 1.5rem;
}

.contact-text {
  font-size: 2rem;
  color: aliceblue;
}

.contact-box {
  width: clamp(100px, 90%, 1000px);
  margin: 80px 50px;
  display: flex;
  flex-wrap: wrap;
}

.contact-links,
.contact-form-wrapper {
  width: 50%;
  padding: 8% 5% 10% 5%;
}

.contact-links {
  background-color: #1f2e43;
  background: radial-gradient(
      circle at 55% 92%,
      #426691 0 12%,
      transparent 12.2%
    ),
    radial-gradient(circle at 94% 72%, #426691 0 10%, transparent 10.2%),
    radial-gradient(
      circle at 20% max(78%, 350px),
      #263a53 0 7%,
      transparent 7.2%
    ),
    radial-gradient(circle at 0% 0%, #263a53 0 40%, transparent 40.2%), #1f2e43;
  border-radius: 10px 0 0 10px;
}

.contact-form-wrapper {
  background-color: #4fd1ac;
  border-radius: 0 10px 10px 0;
}

@media only screen and (max-width: 800px) {
  .contact-links,
  .contact-form-wrapper {
    width: 100%;
  }

  .contact-links {
    border-radius: 10px 10px 0 0;
  }

  .contact-form-wrapper {
    border-radius: 0 0 10px 10px;
  }
}

@media only screen and (max-width: 400px) {
  .contact-box {
    width: 95%;
    margin: 8% 5%;
  }
}

.contact-page h2 {
  font-family: "Arimo", sans-serif;
  color: #fff;
  font-size: clamp(30px, 6vw, 60px);
  letter-spacing: 2px;
  text-align: center;
  transform: scale(0.95, 1);
}

.links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 50px;
}

.link {
  margin: 10px;
  cursor: pointer;
}

.contact-page img {
  width: 45px;
  height: 45px;
  filter: hue-rotate(220deg) drop-shadow(2px 4px 4px #0006);
  transition: 0.2s;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.contact-page img:hover {
  transform: scale(1.1, 1.1);
}

.contact-page img:active {
  transform: scale(1.1, 1.1);
  filter: hue-rotate(220deg) drop-shadow(2px 4px 4px #222) sepia(0.3);
}

.form-item {
  position: relative;
  margin-top: 1rem;
}

.contact-page label,
input,
textarea {
  font-family: "Poppins", sans-serif;
}

.contact-page label {
  position: absolute;
  top: 10px;
  left: 2%;
  color: #999;
  font-size: clamp(14px, 1.5vw, 18px);
  pointer-events: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.contact-page input,
textarea {
  width: 100%;
  outline: 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 12px;
  font-size: clamp(15px, 1.5vw, 18px);
}

input:focus + label,
input:valid + label,
textarea:focus + label,
textarea:valid + label {
  font-size: clamp(13px, 1.3vw, 16px);
  color: rgb(3, 3, 3);
  top: -20px;
  transition: all 0.225s ease;
}

.submit-btn {
  background-color: #fd917e;
  filter: drop-shadow(2px 2px 3px #0003);
  color: #fff;
  font-family: "Poppins", sans-serif;
  font-size: clamp(16px, 1.6vw, 18px);
  display: block;
  padding: 12px 20px;
  margin: 2px auto;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: 0.2s;
}

.submit-btn:hover {
  transform: scale(1.1, 1.1);
}

.submit-btn:active {
  transform: scale(1.1, 1.1);
  filter: sepia(0.5);
}

@media only screen and (max-width: 800px) {
    .contact-page h2 {
    font-size: clamp(40px, 10vw, 60px);
  }
}

@media only screen and (max-width: 400px) {
    .contact-page h2 {
    font-size: clamp(30px, 12vw, 60px);
  }

  .links {
    padding-top: 30px;
  }

  .contact-page img {
    width: 38px;
    height: 38px;
  }
}

